import React, { useState, useEffect } from "react";
import { Div, Button } from "@vkontakte/vkui";

import { ButtonСategory } from "../items";

const ListButton = ({ activeTab, setActiveTab, openAlert }) => {
  return (
    <>
      <Div
        style={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          flexDirection: "row",
          margin: "10px 0px 10px 0px",
        }}
      >
        <ButtonСategory
          activeTab={activeTab}
          setActiveTab={setActiveTab}
          tabType={"general"}
          tabName={"Общая"}
        />
        <ButtonСategory
          activeTab={activeTab}
          setActiveTab={setActiveTab}
          tabType={"likes"}
          tabName={"Лайкают"}
        />
      </Div>

      <Div
        style={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          flexDirection: "row",
          margin: "10px 0px 10px 0px",
        }}
      >
        <ButtonСategory
          activeTab={activeTab}
          setActiveTab={setActiveTab}
          tabType={"hidden-friends"}
          tabName={"Скрытые друзья"}
        />
        <ButtonСategory
          activeTab={activeTab}
          setActiveTab={setActiveTab}
          tabType={"hidding-friends"}
          tabName={"Скрывающие друзья"}
        />
      </Div>
      <Div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          flexDirection: "row",
          margin: "10px 0px 10px 0px",
        }}
      >
        {/* <ButtonСategory
          activeTab={activeTab}
          setActiveTab={setActiveTab}
          tabType={"comments"}
          tabName={"Комментирует"}
          openAlert={openAlert}
        /> */}
        <ButtonСategory
          activeTab={activeTab}
          setActiveTab={setActiveTab}
          tabType={"groups"}
          tabName={"Группы 18+"}
        />
      </Div>
    </>
  );
};

export { ListButton };
