import React, { useState, useEffect } from "react";
import { Div, Button } from "@vkontakte/vkui";

const ButtonСategory = ({
  activeTab,
  setActiveTab,
  tabName,
  tabType,
  openAlert,
}) => {
  return (
    <Button
      style={{
        backgroundColor: activeTab === tabType ? "green" : "#184a6e",
        color: "white",
        width: "48%",
      }}
      mode="commerce"
      size="m"
      onClick={
        tabType === "comments"
          ? () => openAlert("Раздел еще в разработке", "red")
          : () => setActiveTab(tabType)
      }
      stretched={tabType === "groups" ? true : false}
    >
      <span style={{ whiteSpace: tabType === "hidden-friends" ? "none" : "" }}>
        {" "}
        {tabName}
      </span>
    </Button>
  );
};

export { ButtonСategory };
