import React, { useState, useEffect } from "react";
import { Cell, Div, Avatar, Link } from "@vkontakte/vkui";

import { UserCard } from "../components";

const GeneralInfoUser = ({ targetUserInfo }) => {
  return (
    <Div
      className="panelHomeResult"
      // style={{
      //   display: "flex",
      //   justifyContent: "center",
      //   alignItems: "start",
      //   flexDirection: "column",
      //   marginLeft: "10px",
      // }}
    >
      <UserCard
        fullName={targetUserInfo.generalInfo.fullName}
        id={targetUserInfo.generalInfo.id}
        photoURL={targetUserInfo.generalInfo.photoURL}
        type={"getUserInfo"}
        
      />
      <Div>Дата регистрации: {targetUserInfo.generalInfo.regDate}</Div>
      <Div>Последний онлайн: {targetUserInfo.generalInfo.lastSeen}</Div>
      <Div>Город: {targetUserInfo.generalInfo.city}</Div>
      <Div>Аудио: {targetUserInfo.generalInfo.audiosCount}</Div>
      <Div>Фото: {targetUserInfo.generalInfo.photosCount}</Div>
      <Div>Видео: {targetUserInfo.generalInfo.videosCount}</Div>
      <Div>Друзей: {targetUserInfo.generalInfo.friendsCount}</Div>
      <Div>Подписчиков: {targetUserInfo.generalInfo.followersCount}</Div>
    </Div>
  );
};

export { GeneralInfoUser };
